.about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--PrimaryColor);
  /* background: var(--AboutBackground); */
  /* padding: 3.5rem 3.5rem 7rem 3.5rem; */
}
.about-title{
  color: var(--BaseColor);
}
.main-card-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 4rem;
  padding: 2.5rem 1.5rem;
  border-radius: 1rem;
}

.dots-wrap_top {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  align-self: self-start;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
  gap: 0.8rem;
  background-color: var(--BaseColor); 
}

.dots-wrap_bottom {
  width: 100%;
  padding: 1rem;;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  background-color: var(--BaseColor);
}

.dots-wrap_top h2 {
  position: relative;
  left: -5%;
  font-family: "Roboto", sans-serif;
  font-weight: 800;
  color: var(--PrimaryColor);
}



.dots-wrap_bottom a {
  color:  var(--ActionColor);
  margin: 0 1rem;
  font-size: 1.4em;
  transition: color 300ms;
}

.dots-wrap_bottom a:hover {
  color: var(--PrimaryColor);
}

[class*="dot-"] {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  left: -15%;
  top: 0;
}

.dot-1 {
  background: var(--PrimaryColor);
}

.dot-2 {
  background: var(--SecondaryColour);
}

.dot-3 {
  background: var(--ActionColor);
}

.circle-card-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  align-items: center;
  /* flex-shrink: 1;
  flex-grow: 1; */
  justify-content: space-around;
}


.zoom-container {
  display: flex;
  flex-direction: column;
  height: fit-content;
  width: 50%;
  justify-content: center;
  align-items: center;
  /* align-self: center; */
  border-radius: 0.5rem;
  opacity: 1;
  background-color: transparent;
  box-shadow: 0px 0px 3px var(--BaseColor);
  transition: all 600ms cubic-bezier(0.54, -0.34, 0.45, 1.28);
  /* background-color: black; */
}

[class*="inv-com_"] {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
  transition: transform 500ms ease-in-out;
}

.inv-com_1 {
  transform: translateX(-15%);
  perspective: 50rem;
}

.inv-com_2 {
  transform: translateX(15%);
  perspective: 50rem;
}

[class*="illustration_"]{
  width: 45%;
  height: 20rem;
  transition: all 400ms ease-in-out;
}

.illustration_1{
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  background: url("../Assets/5665287_57802-removebg-preview.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--BaseColor);
  transform: rotateY(30deg);
  
}

.illustration_2{
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  background: url("../Assets/5665287_57802-removebg-preview.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--BaseColor);
  transform: rotateY(-30deg);
}

.zoom-container:hover [class*="illustration_"]{
  border-radius: 0;
  transform: rotateY(0deg);
}


.zoom-container:hover [class*="inv-com_"]  {
  transform: translateX(0%);
}

[class*="para-"]{
  width: 100%;
  height: fit-content;
  padding: 1rem 2rem;
  text-align: start;
  word-wrap: break-word;
  color: var(--BaseColor);
  font-size: 1.4em;
  font-family: "Roboto", sans-serif;
  transition: color 500ms ease-in-out;
  background-color: var(--PrimaryColor);
  /* background-color: #c3c3c3; */
  transition: transform 400ms ease-in-out;
}

.para-1{
  transform: translateX(10%);
}

.para-2{
  transform: translateX(-10%);
}


.zoom-container:hover [class*="para-"] {
  transform: translateX(0%);
}


@media screen and (max-width: 1024px) {
  .zoom-container {
    width: 50%;
  }
}

@media screen and (max-width: 768px) {
  .about-me {
    padding: 8%;
    inset: unset;
  }

  [class*="dot-"] {
    left: -6%;
  }

  .zoom-container {
    width: 80%;
  }

  .circle-card-wrapper {
    width: 100%;
    justify-content: space-around;
    padding: 1rem;
  }
}

.images-container {
  display: flex;
  max-width: 500px;
  overflow: scroll;
  scroll-behavior: smooth;
  transition: scroll 0.3s ease-in-out;
}

.image {
  width: 200px;
  height: 200px;
  margin: 5px;
  border-radius: 1rem;
}

.nav-btn {
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  background-color: #c3c3c3;
  border-radius: 6px;
  height: 200px;
  width: 30px;
  margin: 5px;
}

@media screen and (max-width: 600px) {
  .about-me {
    flex-direction: column;
    align-items: center;
    padding: 3.5rem 1.5rem 7rem 1.5rem;
  }

  .zoom-container {
    width: 90%;
  }

  [class*="dot-"] {
    width: 15px;
    height: 15px;
    left: -10%;
  }

  .dots-wrap_top h2 {
    font-size: 1.2em;
  }
}

@media screen and (max-width: 480px) {
  .main-card-wrapper {
    padding: 0;
  }
  .main-card-wrapper {
    padding: 2.5rem 0rem;
  }

  [class*="dot-"] {
    left: -8%;
  }

  .para-1 {
    font-size: 1em;
  }

  .zoom-container {
    width: 100%;
  }
}

@media screen and (max-width: 400px) {
  .about-me {
    padding: 3.5rem 0.8rem 7rem 0.8rem;
  }

  .main-card-wrapper {
    padding: 0;
  }
  .main-card-wrapper {
    padding: 2.5rem 0rem;
  }

  [class*="dot-"] {
    left: -8%;
  }

  .para-1 {
    font-size: 1em;
  }

  .zoom-container {
    width: 100%;
  }
}

@media screen and (max-width: 375px) {
  .para-1 {
    padding: 0.6rem;
  }

  .dots-wrap_top h2 {
    font-size: medium;
    left: -5%;
    font-weight: 800;
  }

  .about-me {
    padding: 3.5rem 0.5rem 7rem 0.5rem;
  }

  .main-card-wrapper {
    padding: 2.5rem 0rem;
  }

  [class*="dot-"] {
    left: -8%;
  }

  .para-1 {
    font-size: 1em;
  }

  .zoom-container {
    width: 100%;
  }
}
